import React from 'react'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import Filters from '../../../components/Sub/Filters/Filters'
import ExportApplications from '../../../components/Sub/ExportDialogs/ExportApplications/ExportApplications'
import EditProfilePopup from '../../../components/Sub/GlobalPopups/EditProfilePopup/EditProfilePopup'
import useApplications from './useApplications'
import useStyles from './ApplicationsPage.style'

const sortFieldsMapping = {
  id: 'id',
  date: 'date',
  studentInfo: 'user_profile_id',
  course: 'course_id',
  status: 'status_id',
  staff: 'staff_id',
}

const ApplicationsPage = ({ setSnack }) => {
  const {
    editOpenState,
    exportOpenState,
    isLoading,
    studentToEdit,
    filtersJSON,
    columns,
    applications,
    filtersHook,
    handleDownload,
    handleCellClick,
    rowsCount,
  } = useApplications()

  const classes = useStyles()
  return (
    <main className={classes.root}>
      <ExportApplications openState={exportOpenState} directory={filtersJSON} />
      <EditProfilePopup openState={editOpenState} setSnack={setSnack} studentToEdit={studentToEdit} />
      <Filters filterType="applications" hook={filtersHook} filtersJSON={filtersJSON} handleDownload={handleDownload} />
      <DataTable
        sx={{ '& .MuiDataGrid-cell': { cursor: 'pointer' } }}
        onCellClick={handleCellClick}
        columns={columns}
        rows={applications}
        loading={isLoading}
        filtersHook={filtersHook}
        rowsCount={rowsCount}
        sortFieldsMapping={sortFieldsMapping}
      />
    </main>
  )
}

export default React.memo(ApplicationsPage)
