import api from '../../../../api/api'
import { API_APPLICATION_BY_ID_ENDPOINT } from '../../../../utils/constants'

const patchApplicationStatus = async (applicationId, status, setIsLoading, applicationState) => {
  const [applications, setApplications] = applicationState
  try {
    setIsLoading(true)
    const response = await api.patch(API_APPLICATION_BY_ID_ENDPOINT(applicationId), status)
    const updatedRow = response.data
    const newApplications = [...applications]
    newApplications[newApplications.findIndex((el) => el.id === updatedRow.id)] = updatedRow
    setApplications(newApplications)
    return Promise.resolve(response.data)
  } catch (e) {
    return Promise.reject(e.response)
  } finally {
    setIsLoading(false)
  }
}

export default patchApplicationStatus
