/* eslint-disable no-nested-ternary */
/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-vars */
import React, { useState, useEffect, useMemo } from 'react'
import { format } from 'date-fns'
import CheckboxGroup from '../../CheckboxGroup/ChekboxGroup'
import ExportDialog from '../ExportDialog/ExportDialog'
import MultipleSelect from '../MultipleSelect/MultipleSelect'
import useAsync from '../../../../hooks/useAsync'
import getCSV from '../../../../utils/getCSV'
import { API_APPLICATIONS_CSV, PROJECT_START_DATE } from '../../../../utils/constants'

const infoArray = [
  {
    label: 'Дата',
    value: 'date',
    checked: true,
  },
  {
    label: 'Курс',
    value: 'course',
    checked: true,
  },
  {
    label: 'ФИО',
    value: 'fio',
    checked: true,
  },
  {
    label: 'Поток',
    value: 'potok',
    checked: true,
  },
  {
    label: 'Email',
    value: 'email',
    checked: true,
  },
]

export default function ExportApplications({ openState, directory }) {
  const { run, error } = useAsync({
    data: [],
    error: null,
  })
  const [open] = openState || false
  const [student, setStudent] = useState(null)
  const [startDate, setStartDate] = useState(PROJECT_START_DATE)
  const [endDate, setEndDate] = useState(Date.now())
  const [types, setTypes] = useState([])
  const [statuses, setStatuses] = useState([])
  const [courses, setCourses] = useState('')
  const [cohorts, setCohorts] = useState([])
  const [cohortsOptions, setCohortsOptions] = useState([])
  const [staff, setStaff] = useState([])
  const [info, setInfo] = useState(infoArray)
  const coursesArray = directory?.courses || []
  const coursesOptions = coursesArray.map((c) => ({ id: c.id, name: c.name }))
  const staffArray = directory?.staff || []
  const staffOptions = staffArray.map((c) => ({ id: c.id, name: `${c.first_name} ${c.last_name}` }))

  const [isCourseError, setIsCourseError] = useState(false)
  const [isCohortsError, setIsCohortsError] = useState(false)
  const [isFiltersError, setIsFiltersError] = useState(false)

  const [serverError, setServerError] = useState(null)

  useEffect(() => setServerError(error), [error])

  useEffect(() => setServerError(null), [staff])

  useEffect(() => {
    if (open) {
      const typesArray = directory?.types || []

      const applicationTypes = typesArray.map((type) => ({
        label: type.value,
        value: type.id,
        checked: true,
      }))

      const statusesArray = directory?.statuses || []
      const applicationStatuses = statusesArray.map((status) => ({
        label: status.value,
        value: status.id,
        checked: true,
      }))

      setTypes(applicationTypes)
      setStatuses(applicationStatuses)
    }
  }, [directory, open])

  useEffect(() => {
    setCohorts([])
    setIsCohortsError(false)
    setIsCourseError(false)
    setServerError(null)
    const options = coursesArray.reduce((acc, course) => {
      if (courses === course?.id) {
        course?.cohorts.map((cohort) =>
          acc.push({
            id: cohort?.id,
            name: `${cohort?.cohort_number}`,
          }),
        )
      }
      return acc
    }, [])
    setCohortsOptions(options?.sort((a, b) => +a?.name - +b?.name))
  }, [courses])

  useEffect(() => {
    if (cohorts) setIsCohortsError(false)
    setServerError(null)
  }, [cohorts])

  useEffect(() => {
    setIsFiltersError(false)
    setServerError(null)
  }, [types, info, statuses])

  useEffect(() => {
    if (open) {
      setStudent(null)
      setCourses('')
      setCohorts([])
      setStaff([])
      setInfo(infoArray)
      setIsCourseError(false)
      setIsCohortsError(false)
      setIsFiltersError(false)
      setServerError(null)
    }
  }, [open])

  const getParams = () => {
    const result = new URLSearchParams()
    // Даты
    result.append('start_date', format(startDate, 'yyyy-MM-dd'))
    result.append('end_date', format(endDate, 'yyyy-MM-dd'))
    // Студент
    if (student) result.append('profile_id', student?.id)
    // Курс + когорты
    if (courses) {
      result.append('course_id', courses)
      cohorts.forEach((id) => result.append('cohort_id', id))
    }
    // Ответственный
    staff.forEach((id) => result.append('staff_id', id))
    // Тип заявки
    types.forEach((type) => type.checked && result.append('type_id', type.value))
    // Общая информация
    info.forEach((item) => item.checked && result.append('fields', item.label.toLowerCase()))
    // Статус
    statuses.forEach((status) => status.checked && result.append('status_id', status.value))

    // // Добавим допполя из-за странной реализации бэка
    if (types.reduce((prev, current) => prev || current?.checked, false)) result.append('fields', 'тип')
    if (statuses.reduce((prev, current) => prev || current?.checked, false)) result.append('fields', 'статус')
    const backParameters = ['ответственный', 'описание']
    backParameters.forEach((item) => result.append('fields', item))

    return result
  }

  const validate = () => {
    const isAnyTypeChecked = types.reduce((prev, current) => prev || current?.checked, false)
    const isAnyInfoChecked = info.reduce((prev, current) => prev || current?.checked, false)
    const isAnyStatusChecked = statuses.reduce((prev, current) => prev || current?.checked, false)
    const isAnyFilterChecked = isAnyTypeChecked || isAnyInfoChecked || isAnyStatusChecked
    setIsFiltersError(!isAnyFilterChecked)
    setIsCourseError(!courses)
    setIsCohortsError(courses && cohorts.length === 0)
    return courses && cohorts.length !== 0 && isAnyFilterChecked
  }

  const onSubmit = () => {
    const isValid = validate()
    if (isValid) run(getCSV(API_APPLICATIONS_CSV, getParams()))
  }

  return (
    <ExportDialog
      openState={openState}
      searchState={[student, setStudent]}
      startDateState={[startDate, setStartDate]}
      endDateState={[endDate, setEndDate]}
      onSubmit={onSubmit}
      isFiltersError={isFiltersError}
      filtersHelpersMessage="Выбери хотя бы один фильтр для выгрузки"
      serverError={serverError}
    >
      <MultipleSelect
        title="Курс"
        placeholder="Выбери курс"
        options={coursesOptions}
        valueState={[courses, setCourses]}
        error={isCourseError}
        helperText="Выбери курс, чтобы выгрузить данные"
      />
      <MultipleSelect
        title="Ответственный"
        placeholder="Выбери ответственного"
        options={staffOptions}
        valueState={[staff, setStaff]}
        multiple
      />
      <CheckboxGroup checkboxes={types} onCheckboxGroupChange={setTypes} headItemTitle="Тип заявки" />
      {cohortsOptions.length > 0 ? (
        <MultipleSelect
          title="Номер потока"
          placeholder="Выбери поток"
          options={cohortsOptions}
          valueState={[cohorts, setCohorts]}
          multiple
          error={isCohortsError}
          helperText="Выбери хотя бы один поток"
        />
      ) : (
        <div>&nbsp;</div>
      )}
      <CheckboxGroup checkboxes={info} onCheckboxGroupChange={setInfo} headItemTitle="Общая информация" />
      <CheckboxGroup checkboxes={statuses} onCheckboxGroupChange={setStatuses} headItemTitle="Статус заявки" />
    </ExportDialog>
  )
}
