import api from '../../../../api/api'
import { API_APPLICATIONS_ENDPOINT } from '../../../../utils/constants'

const getApplications = async (setIsLoading, setApplications, filters, setRowsCount) => {
  try {
    setIsLoading(true)
    const response = await api.get(API_APPLICATIONS_ENDPOINT, {
      params: { ...filters, profile_id: filters?.studentId, studentId: undefined },
    })
    setApplications(Object.values(response.data?.results))
    setRowsCount(response.data?.count)
    return Promise.resolve(response.data?.results)
  } catch (e) {
    return Promise.reject(e.response)
  } finally {
    setIsLoading(false)
  }
}

export default getApplications
